.navbar {
  width: 100%;
  /* border: 1px solid #fe7e30; */
}

.navbar_main_div {
  width: 90%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  max-width: 1247px;
  margin: auto;
  align-items: center;
  position: sticky;
  padding-bottom: 50px;
}
.navbar_part_a {
  width: 3%;
  right: 100%;
  top: 0;
}

.navbar_part_b {
  width: 50%;
  display: flex;
  gap: 10px;
  /* border: 1px solid red; */
  align-items: center;
  justify-content: space-around;
}
.navbar_part_b > div > a {
  text-decoration: none;
  color: #fe7e30;
}
.navbar_part_b > div > a {
  display: inline-block;
  padding-bottom: 10px;
  position: relative;
}
.navbar_part_b > div > a::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 1px;
  color: #fe7e30;
  background-color: #fe7e30;
  transition: width 0.35s ease-out;
}
.navbar_part_b > div > a:hover::before {
  width: 100%;
}
